.recipe {
    border-radius: 10px;
    box-shadow: 0px 5px 20px #7F5539;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    padding: 5px;
}

.image {
    border-radius: 25%;
    width: 250px;
    height: 250px;
}

.title{
    color: #7F5539;
}